import { useState, forwardRef } from "react";
import DatePicker from "react-datepicker";
import "views/style/datepicker.css";
import { registerLocale } from "react-datepicker";  // 
import ko from 'date-fns/locale/ko'; // 

registerLocale("ko", ko) // 

function PeriodSearch({change, startdate, enddate}) {
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const [btnClicked, setBtnClicked] = useState("");
  const ExampleCustomInput = forwardRef(({ value, onClick }, ref) => (
    <div className="example-custom-input" onClick={onClick} ref={ref}>
      <div className="mr-1">
      <i className="bi bi-calendar-day mr-3"></i>              
      {value}
      </div>
    </div>
  ));

  const dateSubmit = () => {
    
    setStartDate(startDate);
    setEndDate(endDate);
    change(startDate, endDate) //mysql between ~  . 1 
  }

  const handleBtnClicked = (e) => {
    const { value } = e.target;
    setBtnClicked(value);
    const currentDate = new Date(); 
    //  
    if (value === "") {
      setStartDate(new Date());
      setEndDate(new Date());
    }
    // 3   
    if (value === "3") {
      let threeDaysAgo = new Date(
        currentDate.getTime() - 3 * 24 * 60 * 60 * 1000
      );
      setStartDate(threeDaysAgo);
      setEndDate(new Date());
    }
    // 1   
    if (value === "1") {
      let weekAgo = new Date(currentDate.getTime() - 7 * 24 * 60 * 60 * 1000);
      setStartDate(weekAgo);
      setEndDate(new Date());
    }
    // 1   
    if (value === "1") {
      let oneMonthAgo = new Date(
        new Date().getFullYear(),
        new Date().getMonth() - 1,
        new Date().getDate()
      );
      setStartDate(oneMonthAgo);
      setEndDate(new Date());
    }
    // 3   
    if (value === "3") {
      let threeMonthAgo = new Date(
        new Date().getFullYear(),
        new Date().getMonth() - 3,
        new Date().getDate()
      );
      setStartDate(threeMonthAgo);
      setEndDate(new Date());
    }
  };

  return (
    <div>
            <div className="mt-3 mb-3" style={{backgroundColor: "#EDECEA"}}>
              <div className="ml-3 pt-2">
                <button type="button" className="btn btn-dark btn-sm mr-1" value="" onClick={ handleBtnClicked }></button>
                <button type="button" className="btn btn-dark btn-sm mr-1" value="3" onClick={ handleBtnClicked }>3</button>
                <button type="button" className="btn btn-dark btn-sm mr-1" value="1" onClick={ handleBtnClicked }>1</button>
                <button type="button" className="btn btn-dark btn-sm mr-1" value="1" onClick={ handleBtnClicked }>1</button>
                <button type="button" className="btn btn-dark btn-sm mr-1" value="1" onClick={ handleBtnClicked }>3</button>
              </div>
              <div className="row ml-3 mt-3 mb-2 pb-2">
              <div className="row ml-1">
                <div className="mr-4">
                  <div>From: </div>
                <DatePicker
                  selected={startDate}
                  onChange={(date) => setStartDate(date)}
                  customInput={<ExampleCustomInput />}
                  dateFormat="yyyy-MM-dd"
                  locale={ko}
                />
                </div>
              </div>
              <div className="row ml-3">
                <div className="mr-4">
                <div>To: </div>
                <div className="row ml-0">
                <DatePicker
                  selected={endDate}
                  onChange={(date) => setEndDate(date)}
                  customInput={<ExampleCustomInput />}
                  dateFormat="yyyy-MM-dd"
                  locale={ko}
                />
                </div>
                </div>
              </div>
              </div>
              <button type="submit" className="btn btn-dark btn-block" onClick={ dateSubmit }>Submit</button>
            </div>
          </div>
  );
}
export default PeriodSearch;