let treatmentslist=[];
for(var i=1; i<1; i++){
    const TR = {treatmentid:"treatmentid"+i, memo:"memo"+i,treatmentdate:new Date().toLocaleDateString(),patientid:"patientid"+i,userid:"userid"+i,status:"" } 
    treatmentslist.push(TR);
}
// 
export function getAllTreatmentsData(){
    return treatmentslist;
}
//   
export function isReceptionTreatment(patient_id){
    return treatmentslist.filter((item)=>(item.patientid===patient_id &&item.status==="" )).length<1?true:false
}
//  
export function DeleteReceptionTreatment(treatment_id){
    const index = treatmentslist.findIndex((item)=>(item.treatmentid===treatment_id))
    if(index>=0){
        treatmentslist.splice(index,1)
    }
}

//
export function ReceptionTreatment(patient_id, user_id){
    const tid = treatmentslist.length+1
    const newTreatment = {treatmentid:"treatmentid"+tid, memo:"",treatmentdate:new Date().toLocaleDateString(),patientid:patient_id,userid:user_id,status:"" }
    treatmentslist.push(newTreatment)
    return newTreatment;
}


