import { useEffect, useState } from "react";
import { Button, Form, Spinner } from "react-bootstrap";
import { updateForm, deleteUser, disableUser, enableUser } from "apis/User";

const errorMsg = {
  password_empty : ' .',
  userid_empty : ' .',
  err_confirmPassword: '  .',
  fail_update:" ."
}

function UpdateForm(props) {
 
  const [validated, setValidated] = useState(false);
  const [errorMessage, setErrorMesssage] = useState(errorMsg.password_empty);
  const [isInvalid, setIsInvalid] = useState(false);
  const [loading, setLoading] = useState(false);
  const [formData, setFormData] = useState({});

  const handleChange = (event) => {
    setFormData({
      ...formData,
      [event.target.name]:event.target.value
    })
  }

  useEffect(() => {
    setFormData({
      ...props.updateUser,
      userpassword:"",
      confirmpassword:""
    })
  }, [props.updateUser]);

  const handleSubmit = (event) => {
    setLoading(true);
    const form = event.currentTarget;
    event.preventDefault();
    event.stopPropagation();
    if (form.checkValidity() === false) {// 
      setValidated(true);
      setLoading(false);
      setErrorMesssage(errorMsg.password_empty);
    } else { // 
      if(formData.userpassword !== formData.confirmpassword){//,   
        setIsInvalid(true);
        setErrorMesssage(errorMsg.err_confirmPassword);
        setFormData({
          ...formData,
          confirmpassword:""
        })
        setLoading(false);
      } else {
        
        const response = updateForm(formData);
        response.then((response) => {
          if(response.data.state === "success") {
            setIsInvalid(false);
            props.setIsUpdate(false);
            props.updateShow("  .");
          } else {
            setIsInvalid(true);
            setErrorMesssage(errorMsg.fail_update);
          }
        }).catch((error) => {
          setIsInvalid(true);
          setErrorMesssage(errorMsg.fail_update);
        }).finally(() => {
          setLoading(false);

        })
      }
    }
  };

  const deleteUserFun = () => {

    if(window.confirm(" ?") === true){
      setLoading(true);
      const response = deleteUser(formData.userid);
      response.then((response) => {
        if(response.data.state === "success"){
          setIsInvalid(false);
          setLoading(false);
          props.setIsUpdate(false);
          props.updateShow(" .");
          props.getUsers();
        } else {
          setIsInvalid(true);
          setErrorMesssage(errorMsg.fail_update);
          setLoading(false);
        }
      }).catch((error) => {
          setIsInvalid(true);
          setErrorMesssage(errorMsg.fail_update);
          setLoading(false);
      })

    }
  }

  const disableUserFun = () => {
    if(window.confirm("  ?") === true){
      setLoading(true);
      const response = disableUser(formData.userid);
      response.then((response) => {
        if(response.data.state === "success"){
          setIsInvalid(false);
          setLoading(false);
          props.setIsUpdate(false);
          props.updateShow("  .");
          props.getUsers();
        } else {
          setIsInvalid(true);
          setErrorMesssage(errorMsg.fail_update);
          setLoading(false);
        }
      }).catch((error) => {
          setIsInvalid(true);
          setErrorMesssage(errorMsg.fail_update);
          setLoading(false);
      })
    }
  }

  const enableUserFun = () => {
    if(window.confirm("  ?") === true){
      setLoading(true);
      const response = enableUser(formData.userid);
      response.then((response) => {
        if(response.data.state === "success"){
          setIsInvalid(false);
          setLoading(false);
          props.setIsUpdate(false);
          props.updateShow("  .");
          props.getUsers();
        } else {
          setIsInvalid(true);
          setErrorMesssage(errorMsg.fail_update);
          setLoading(false);
        }
      }).catch((error) => {
          setIsInvalid(true);
          setErrorMesssage(errorMsg.fail_update);
          setLoading(false);
      })
    }
  }



  return (
    <Form noValidate validated={validated} onSubmit={handleSubmit}>
      <Form.Group style={{marginBottom:"10px"}}>
        <Form.Label></Form.Label>
        <Form.Control required name="userid" type="text" placeholder="User ID" value={formData.userid || ""} readOnly/>
      </Form.Group>

      <Form.Group style={{marginBottom:"10px"}}>
        <Form.Label></Form.Label>
        <Form.Control required name="userpassword" type="password" placeholder="Password" value={formData.userpassword || ""} onChange={handleChange}/>
        <Form.Control.Feedback type="invalid"> .</Form.Control.Feedback>
      </Form.Group>

      <Form.Group style={{marginBottom:"10px"}}>
        <Form.Label> </Form.Label>
        <Form.Control required name="confirmpassword" type="password" placeholder="Confirm Password" value={formData.confirmpassword || ""} onChange={handleChange} isInvalid={isInvalid}/>
        <Form.Control.Feedback type="invalid">{errorMessage}</Form.Control.Feedback>
      </Form.Group>

      <Form.Group style={{marginBottom:"10px"}}>
        <Form.Label></Form.Label>
        <Form.Control required name="username" type="text" placeholder="User Name" value={formData.username || ""} readOnly/>
      </Form.Group>

      <Form.Group style={{marginBottom:"10px"}}>
        <Form.Label></Form.Label>
        <Form.Control name="phonenumber" type="test" placeholder="Phone Number" value={formData.phonenumber || ""} readOnly/>
      </Form.Group>

      <Form.Group style={{marginBottom:"10px"}}>
        <div>
          <Form.Label></Form.Label>
        </div>
        <Form.Check checked={formData.sex === ""} inline label="" name="sex" type={"radio"} value="" feedback={" ."} readOnly/>
        <Form.Check checked={formData.sex === ""} inline label="" name="sex" type={"radio"} value="" feedback={" ."} readOnly/>
        <Form.Control.Feedback type="invalid"> .</Form.Control.Feedback>
      </Form.Group>

      <Form.Group style={{marginBottom:"10px"}}>
        <div>
          <Form.Label></Form.Label>
        </div>
        <Form.Check checked={formData.role_authority === "ROLE_DOCTOR"} required inline label="" name="role_authority" value="ROLE_DOCTOR" type={"radio"} feedback={" ."} readOnly/>
        <Form.Check checked={formData.role_authority === "ROLE_NURSE"} required inline label="" name="role_authority" value="ROLE_NURSE" type={"radio"} feedback={" ."} readOnly/>
        <Form.Control.Feedback type="invalid"> .</Form.Control.Feedback>
      </Form.Group>

      <Form.Group style={{marginBottom:"10px"}}>
        <Form.Label></Form.Label>
        <Form.Control name="userroom" type="text" placeholder="Treatment Room" value={formData.userroom || ""} onChange={handleChange} readOnly/>
      </Form.Group>

      <Form.Group style={{marginBottom:"10px"}}>
        <Form.Label></Form.Label>
        <Form.Control type="text" value={formData.userenabled ===1 ? "" : ""} readOnly/>
      </Form.Group>

      <div className="text-right">
        {formData.userenabled === 1 ? 
        <Button className="w-25 mr-3" type="submit" variant="dark">
        {loading === true ? <Spinner className="mr-2" as="span" animation="border" size="sm" role="status"/> : null}
           </Button>
        : 
        <Button className="w-25 mr-3" variant="primary" type="button" onClick={enableUserFun}>
        {loading === true ? <Spinner className="mr-2" as="span" animation="border" size="sm" role="status"/> : null}
          </Button>
        }


        {formData.userenabled === 1 ? 
        <Button className="w-25" variant="danger" type="button" onClick={disableUserFun}>
        {loading === true ? <Spinner className="mr-2" as="span" animation="border" size="sm" role="status"/> : null}
          </Button>
        :
        <Button className="w-25" variant="danger" type="button" onClick={deleteUserFun}>
        {loading === true ? <Spinner className="mr-2" as="span" animation="border" size="sm" role="status"/> : null}
          </Button>
        }
        
      </div>
    </Form>
  );
}

export default UpdateForm;