const initialState = {
  reservation: {}
}
//  
const SET_RESERVATION="reservation/setReservation";


//   
export const createSetReservation = (reservation) => {
  return {type:SET_RESERVATION, reservation};
} 

// 
const reservationReducer = (state = initialState, action) => {
  if(action.type === SET_RESERVATION) {
    return {...state, reservation:action.reservation};
  } else {
    return state;
  }
}


export default reservationReducer;